.container {
  max-width: 1600px;
  width: 100%;
  padding: 0 15px;
  margin: 0 auto;
}

.hidden {
  display: none;
}

.burger {
  padding: 0;
  margin: 0;
  display: none;
  position: relative;
  border: none;
  background: none;
  height: 21px;
  width: 30px;
  cursor: pointer;
}

.burger-line {
  width: 30px;
  height: 3px;
  background-color: #fff;
  transition: 0.3s all ease;
}

.burger-line:nth-child(2) {
  width: 20px;
}

.top-line {
  position: absolute;
  top: 0;
  right: 0;
}

.mid-line {
  position: absolute;
  left: 0;
  top: 50%;
  transform: translate(0, -50%);
}

.bottom-line {
  position: absolute;
  left: 0;
  bottom: 0;
}

.burger.active > .top-line {
  transform: translateY(8px) rotate(45deg);
  transition: 0.3s all ease;
}

.burger.active > .mid-line {
  opacity: 0;
  transition: 0.3s all ease;
}

.burger.active > .bottom-line {
  transform: translateY(-10px) rotate(-45deg);
  transition: 0.3s all ease;
}

